import React, { useState } from 'react';
import { Github, Mail, Lock, Loader2, AlertCircle } from 'lucide-react';
import { Alert, AlertDescription } from '@/components/ui/alert';

// Shared button component
const Button = ({ children, variant = 'primary', isLoading = false, ...props }) => {
  const baseStyles = "w-full flex justify-center items-center py-2 px-4 border rounded-md shadow-sm text-sm font-medium focus:outline-none focus:ring-2 focus:ring-offset-2";
  const variants = {
    primary: "border-transparent text-white bg-indigo-600 hover:bg-indigo-700 focus:ring-indigo-500",
    google: "border-transparent text-white bg-red-600 hover:bg-red-700 focus:ring-red-500",
    github: "border-transparent text-white bg-gray-800 hover:bg-gray-900 focus:ring-gray-500",
    linkedin: "border-transparent text-white bg-blue-600 hover:bg-blue-700 focus:ring-blue-500",
    outline: "border-gray-300 text-gray-700 bg-white hover:bg-gray-50 focus:ring-indigo-500"
  };

  return (
    <button 
      className={`${baseStyles} ${variants[variant]}`} 
      disabled={isLoading}
      {...props}
    >
      {isLoading ? <Loader2 className="h-4 w-4 mr-2 animate-spin" /> : null}
      {children}
    </button>
  );
};

// Input field component
const Input = ({ label, error, ...props }) => (
  <div>
    <label className="block text-sm font-medium text-gray-700 mb-1">
      {label}
    </label>
    <input
      className={`block w-full border ${error ? 'border-red-300' : 'border-gray-300'} 
        rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 
        focus:border-indigo-500 sm:text-sm`}
      {...props}
    />
    {error && (
      <p className="mt-1 text-sm text-red-600">{error}</p>
    )}
  </div>
);
